.custom-block-1 {
  max-width: 630px;
  margin-left: auto;
  margin-right: auto;
  position: relative;

  .background-many-gradient {
    padding: 30px 15px 15px;
    @include media-breakpoint-up('sm') {
      padding: 50px 30px 20px;
    }
  }

  .image-video-first, .image-video-last {
    transform: scale(1.9);
    // opacity: 0;
    margin-left: 15px;
    margin-right: 15px;
    max-width: calc(100% - 30px);
    @include media-breakpoint-up('sm') {
      margin-right: 0;
      margin-left: 0;
      max-width: 100%;
      transform: scale(2);
    }  
  }
  .image-video-first, #boxVideo  {
    margin-top: -15%;
    margin-bottom: -15%;
  } 
  .image-video-first {
    // position: absolute;
  }

  .image-video-last {
    margin-bottom: 10%;
  }

  &__svg {
    width: 21%;
    height: auto;
    display: block;
    margin-left: auto;
    margin-bottom: -31%;
  }
  
  #boxVideo {
    width: 200% !important;
    height: auto !important;
    position: absolute;
    transform: translate(-25%, -25%);
    top: 0;
    left: 0;
    // margin-top: -30%;
    // &::before {
    //   content: '';
    //   padding-top: 100%;
    //   display: block;
    //   width: 0;
    // }
    .owl-stage {
      transition: all 0s !important;
      will-change: transform;
    }
    img {
      // position: absolute;
      // top: 0;
      // left: 0;
      // width: 100%;
      // height: 100%;
      // visibility: hidden;
      // opacity: 0;
      // z-index: -1;
      &.active {
        // visibility: visible;
        // opacity: 1;
        // z-index: 1;
      }
    }
    // canvas {
    //   position: absolute;
    //   top: 0;
    //   left: 0;
    //   bottom: 0;
    //   right: 0;
    //   width: 100%;
    //   height: 100%;
    // }
  }

  &__decor {
    display: none;
    @include media-breakpoint-up('md') {
      display: block;
    }

    &__img {
      position: absolute;
    }

    &__img--1 {
      top: (28900%/1789);
      right: 113%;
    }

    &__img--2 {
      top: (75000%/1790);
      right: 95%;
    } 

    &__img--3 {
      bottom: 40px;
      right: 107%;
    }

    &__img--4 {
      top: 45%;
      left: 115%;
    }

    &__img--5 {
      bottom: 0;
      right: -635px;
      transform: translate(0%, 30%);
      @media (min-width: 1830px) {
        right: calc(315px - 50vw);
      }
    }
  }
}